
import XHeader from 'vux/src/components/x-header/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'

import Scroll from 'assembly/scroll/scroll'
import Loading from 'assembly/loading/loading'

const PAGESIZE = 20
export default {
components: {
XHeader,
Divider,
Scroll,
Loading,
Datetime
},
data () {
return {
disArr: [],
listenScroll: true,
hasMore: true,
pageNo: 1,
time: '',
visibility: false,
pullup: true,
inited: false 
}
},
methods: {
loadMore () {
if (!this.hasMore) {
return
}
this.pageNo ++
this._getExchangeList()
},
toDetail (id) {
this.$router.push({
path: '/exchangeDetail',
query: {
id: id
}
})
},
change () {
this.pageNo = 1
this.disArr = []
this.hasMore = true
this._getExchangeList()
},
onCancel () {
this.time = ''
this._getExchangeList()
},
formatePrice (num) {
return num / 100
},
_getExchangeList () {
if (this.inited) {
this.$vux.toast.text('拼命请求中，请稍候')
return
}
this.inited = true
let data = {
pagingQuery: {
pageIndex: this.pageNo,
pageSize: PAGESIZE
}
}
if (this.time) {

data.startTime = this.time + '-01'
data.endTime = this.$util.getMonthLastDay(this.time)
}
this.$tupHttp(this, 'front/account/cash/v1', 'POST', data).then(res => {
this.inited = false
let disArr = res.data.models.map(item => {
item.sum = item.orderAmount ? this.$util.formatThousand(this.formatePrice(item.orderAmount)) : this.$util.formatThousand(this.formatePrice(item.payAmount))
item.availableLimit = this.$util.formatThousand(this.formatePrice(item.availableLimit))
item.commitTime = new Date(item.commitTime).format('yyyy-MM-dd hh:mm:ss')
return item
})
if (this.pageNo === 1 && !this.disArr.length) {
this.disArr = disArr
} else {
this.disArr = this.disArr.concat(disArr)
}
if (res.data.models.length < PAGESIZE) { // 是否有下一页
this.hasMore = false
}
setTimeout(() => {
this.$refs.scroll.refresh()
}, 500)
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
}
},
created () {
this._getExchangeList()
}
}



